.projectsBlock {

    background-color: #f0e8f9;
    display: flex;
    flex-wrap: wrap;
}

.projects {
    border: 1px solid;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;


}

.title {
    background-color: #fcfcfc;
}

.projectsContainer {
    /*align-items: center;*/
    flex-direction: column;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;

}

.projectsC {
    /*align-items: center;*/
    flex-direction: column;
    justify-content: flex-start;
}